.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: none;
  background-color: rgb(0, 0, 0, 0.5);
  &.active {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &Content {
    padding: 25px;
    background-color: white;
    border-radius: 16px;
    min-width: 250px;
  }

}